    //Center zoom
    //http://stackoverflow.com/questions/19304574/center-set-zoom-of-map-to-cover-all-visible-markers
    HomePage.prototype.bounceMap = function (markers) {
        var bounds = new google.maps.LatLngBounds();
        for (var i = 0; i < markers.length; i++) {
            bounds.extend(markers[i].getPosition());
        }
        this.map.fitBounds(bounds);
    };
    HomePage.prototype.resizeMap = function () {
        var _this = this;
        setTimeout(function () {
            google.maps.event.trigger(_this.map, 'resize');
        }, 200);
    };
    HomePage.prototype.getCurrentPositionfromStorage = function (markers) {
        var _this = this;
        this.storage.get('last_ocation').then(function (result) {
            if (result) {
                var myPos = new google.maps.LatLng(result.lat, result.long);
                _this.map.setOptions({
                    center: myPos,
                    zoom: 9
                });
                var marker = _this.addMarker(myPos, "Mon ancienne position sauvegardée: " + result.location, null);
                markers.push(marker);
                // this.bounceMap(markers);
                _this.resizeMap();
            }
        });
    };
